.menu-wrapper {
  width: 100%;
  display: block;
  position: relative;
  margin: 0 0 10px 0;
  padding: 10px 10px 2px 0;
  background: #f1f1f1;
}

.trapezoid {
  border-bottom: 34px solid #fff;
  border-left: 25px solid #fb232300;
  border-right: 25px solid transparent;
  height: 0;
  min-width: 15%;
  text-align: center;
  border-top-left-radius: 125px;
  border-top-right-radius: 125px;
  font-size: 13px;
  margin-left: -15px;
  cursor: pointer;
}

.trapezoid span {
  position: relative;
  top: 8px;
}

.tab-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

p.external-zdx {
  color: #ff6400;
  text-decoration: none;
}

p.external-zdx a {
  color: #ff6400;
  text-decoration: none;
}


.tab-container .icon {
  border: 2px solid #00000069;
  border-radius: 20px;
  padding: 0px 2px;
  cursor: pointer;
}

.tab-container .icon.last {
  margin-left: auto;
}

.trapezoid:nth-child(8) {
  z-index: 1;
}

.trapezoid:nth-child(7) {
  z-index: 2;
}

.trapezoid:nth-child(6) {
  z-index: 3;
}

.trapezoid:nth-child(5) {
  z-index: 4;
}

.trapezoid:nth-child(4) {
  z-index: 5;
}

.trapezoid.active {
  border-bottom: 34px solid #2196f3;
  color: #fff;
}

.trapezoid:nth-child(3) {
  z-index: 6;
}

.trapezoid:nth-child(2) {
  z-index: 7;
}

.trapezoid:nth-child(2) {
  z-index: 8;
  margin-left: 0 !important;
}

.hidden {
  display: none;
}